'use strict';

function stanAlert() {
    /**
     *  éç¨ç alert æ¹æ³ï¼è­¦ååå®¹æ¯æ html æ ¼å¼æ ç­¾
     *  @param {object} [options] ï¼å¯éï¼ç¸å³åæ°ãè®¾å®
     *      {string}  [type]         ï¼å¯éï¼æå® alert ç±»åï¼å¯é infoãdangerãwarningãsuccessï¼é»è®¤ä¸º danger
     *      {string}  [title]        ï¼å¯éï¼æå® alert åå®¹çå°æ é¢
     *      {string}  [content]      ï¼å¯éï¼æå® alert åå®¹çæ­£æ
     *      {boolean} [autoClose]    ï¼å¯éï¼æå® alert æ¯å¦èªå¨éèï¼é»è®¤ true ï¼ä¼ å¥çåæ°ä¼è¢«ç±»åè½¬æ¢
     *      {number}  [shownExpires] ï¼å¯éï¼æå® alert å¤å°ç§åèªå¨éèï¼é»è®¤ 3 ç§ï¼è¥ä¼ å¥ç autoClose ä¸º false ï¼æ­¤åæ°å°ä¸çæ
     *      {string}  [textAlign]    ï¼å¯éï¼æå® alert ä¸­ä¸»ä½åå®¹çææ¡å¯¹é½æ¹å¼ï¼é»è®¤ left ï¼å¯ä¼ å¥å¼ä¸º left/center/right
     */
    var typeMap = {
        'info': 'info',
        'danger': 'danger',
        'warning': 'warning',
        'success': 'success',
    };
    var alignMap = {
        'left': 'left',
        'center': 'center',
        'right': 'right',
    };
    var options = arguments[0] || {};
    var alertType = options.type ? (typeMap[options.type] || 'danger') : 'danger';
    var alertTitle = options.title ? `<strong>${options.title}</strong><br/>` : '';
    var alertContent = options.content || '';
    var autoClose  = Boolean((options.autoClose === undefined) ? true : options.autoClose);
    var shownExpires = Number((options.shownExpires === undefined) ? 3 : options.shownExpires);
    var textAlign = options.textAlign ? (alignMap[options.textAlign] || 'left') : 'left';
    var alertDom = '' +
        `<div class="stan-alert-container">
            <div class="alert alert-${alertType}" role="alert">
                <button type="button" class="close"><span aria-hidden="true">&times;</span></button>
                <div class="alert-content">
                    ${alertTitle}
                    <div class="text-${textAlign}">${alertContent}</div>
                </div>
            </div>
        </div>`;
    var autoCloseFunc;

    if (autoClose) {
        autoCloseFunc = setTimeout(function() {
            $('.stan-alert-container').fadeOut('slow', function() {
                $(this).remove();
            });
        }, shownExpires * 1000);
    }

    $('body').append(alertDom);
    $('.stan-alert-container button.close').on('click', function() {
        $('.stan-alert-container').fadeOut('fast', function() {
            $(this).remove();
            clearTimeout(autoCloseFunc);
        });
    });
}

module.exports = stanAlert;